import infoBox from "../../components/boxstyle/infobox.vue"
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import pickerRegion from "../../components/pickerregion.vue"
import timePicker from "../../components/timePicker.vue"
import btn from "../../components/boxstyle/buttonstyle.vue"
import hTimePicker from "@/components/h-timePicker/h-timePicker.vue";

import "../../../common/itemcontent.css"

export default {
	data() {
		return {
			// 选车内容
			infoList: [],
			carList: [],
			carIdList: [],
			// 承运方向 
			gotPlanList: [],
			planList: [],
			planListId: [],
			current: 0,
			current1: 0,
			current2: 0,
			// 判断是哪个页面
			// popupList: ["popup","popup2","popup3"],
			popName: '',
			// 当前时间列表
			timeList: [
				[],
				[],
				[],
				[],
				[]
			],
			// 选中的预计到厂时间
			arriveTime: "",
			// 判断时间是否在这之后 false 正确
			isTure: ""
		}
	},

	methods: {
		// 接收子组件的传值
		showIndex(data) {
			console.log(data)
			if (data.length === 0) {
				this.current = 0
			} else {
				this.current = data[1];
			}
		},

		showStatus(data) {
			if (data) {
				this.$refs.popup.close();
			}
		},

		showIndex1(data) {
			console.log(data)
			if (data.length === 0) {
				console.log(1)
				this.current1 = 0
			} else {
				this.current1 = data[1];
			}
		},

		showStatus1(data) {
			if (data) {
				this.$refs.popup2.close();
			}
		},
		
		changeTime(e) {
			console.log(e)
			this.arriveTime = e
			console.log(e)
		},
		
		// 比较时间大小
		compareDate(date1, date2) {
			var oDate1 = new Date(date1);
			var oDate2 = new Date(date2);
			if (oDate1.getTime() > oDate2.getTime()) {
				return true; //第一个大
			} else {
				return false; //第二个大
			}
		},

		toChooseCar() {
			this.$refs.popup.open()
		},

		toChoosePlan() {
			this.getPlanInfo();
			this.$refs.popup2.open()
		},

		// toChooseTime() {
		// 	this.$refs.popup3.open()
		// },

		// 获取推送
		shouquan() {
			const tmplIds = 'P0yJj1sqB8oNODpPd5TpUA9I-qMCM-tih783bIpLZLQ'
			wx.requestSubscribeMessage({
				tmplIds: ["P0yJj1sqB8oNODpPd5TpUA9I-qMCM-tih783bIpLZLQ"],
				subscription_type: 'permanent',
				success(res) {
					console.log("成功", res)
				},
				fail(res) {
					console.log("失败", res)
				}
			})
		},

		// 获取页面内信息
		async getCarInfo() {
			const opts = {
				url: "/sc/carMng/getCanUseCars",
				method: "post"
			}

			const res = await this.$http.httpTokenRequest(opts);
			this.infoList = res.data.result;
			this.infoList.map((item, index) => {
				this.carList.push(item.carNo);
			})
			this.infoList.map((item, index) => {
				this.carIdList.push(item.carId);
			})
		},

		async getPlanInfo() {
			const opts = {
				url: "/personal/driver/getTaskAppRouteList",
				method: "post"
			}
			const res = await this.$http.httpTokenRequest(opts);
			this.gotPlanList = res.data.result;
			this.planList = [];
			this.planListId = [];
			this.gotPlanList.map((item, index) => {
				this.planList.push(item.routeName);
			})
			this.gotPlanList.map((item, index) => {
				this.planListId.push(item.id);
			})
		},

		// 提交并判断是否跳转
		async submitApply() {
			// 提交信息
			uni.showLoading({});
			if (this.isTure) {
				uni.showModal({
					content: "请输入大于当前时间的预计时间"
				})
			} else {
				if(this.planList[this.current]!== undefined && this.carList[this.current]!== undefined) {
					const params = {
						carId: this.carIdList[this.current],
						carNo: this.carList[this.current],
						destination: this.planList[this.current1],
						estimatedTime: this.arriveTime,
						routeId: this.planListId[this.current1]
					}
					
					const opts = {
						url: "/personal/driver/modTaskAppRoute",
						method: "post"
					}
					
					console.log(this.arriveTime);
					const res = await this.$http.httpTokenRequest(opts, params);
					
					console.log(res);
					if (res.data.code === 0) {
						uni.hideLoading({});
						this.shouquan();
						uni.navigateBack({
							delta: 1
						});
					}
					if(res.data.code === 1) {
						uni.showModal({
							title: res.data.desc,
							icon: "none"
						})
					}
				} else {
					setTimeout(()=>{
						uni.hideLoading({});
						uni.showModal({
							content: "请填写内容"
						})
					},700)
				}
			}

		},
	},

	onLoad() {
		this.getCarInfo();
		console.log(this.timeList)
	},

	components: {
		infoBox,
		uniPopup,
		pickerRegion,
		btn,
		timePicker,
		hTimePicker
	}
}
